@import url(https://fonts.googleapis.com/css2?family=Nunito:ital@1&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

body {
	background: #fceabb; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to left,
		#f8b500,
		#fceabb
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	font-family: 'Nunito', sans-serif;
}

.App {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	position: relative;
	width: 100%;
}

.logo {
	position: absolute;
	top: 0;
	left: 0;
	margin: -30px 0;
	width: 175px;
}

ul {
	list-style: none;
}

.suggestions {
	padding: 20px;
	border-radius: 5px;
	background-color: rgba(255, 255, 255, 0.6);
	text-align: center;
}

.suggestions li {
	cursor: pointer;
	margin: 5px 0;
	transition: transform 0.2s ease;
}

.suggestions li:hover {
	transform: scale(1.1);
}

.city-btn {
	cursor: pointer;
}

.EventList, .data-vis-wrapper {
	display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  width: 100%;
  justify-content: center;
}

.data-vis-wrapper {
  margin: 10px;
  background-color: rgba(255, 255, 255, 0.6);
}

.event {
	background-color: whitesmoke;
	border-radius: 5px;
	padding: 50px;
  margin: 10px;
}

.event * {
  margin: 10px 0;
}

input {
	padding: 10px;
	margin: 5px;
	border-radius: 5px;
	border: none;
	font-size: 1rem;
}

input:focus {
	border: none;
	outline: none;
	box-shadow: none;
}

button {
	border: none;
	padding: 10px;
	border-radius: 5px;
	cursor: pointer;
	background-color: #fcae1e;
	font-size: 1.2rem;
	color: whitesmoke;
}

#back-btn {
  background-color: whitesmoke;
  color: #494949;
}

button:hover {
	filter: brightness(110%);
}

.Alert {
  margin: 10px 0;
}

.Policy {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.Policy h1 {
  margin: 20px 0;
}

.Policy article {
  background-color: whitesmoke;
  padding: 10px;
}

.WelcomeScreen {
	width: 100%;
	height: 100%;
	top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.login-button {
	color: #494949;
	text-transform: uppercase;
	text-decoration: none;
	background: #ffffff;
	border: 4px solid #494949;
	display: inline-block;
	transition: all 0.4s ease 0s;
	min-width: 10rem;
	margin: 1rem;
}

.google-btn {
	width: 184px;
	height: 42px;
	margin: 1rem;
	background-color: #4285f4;
	border-radius: 2px;
	box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-icon-wrapper {
	position: absolute;
	margin-top: 1px;
	margin-left: 1px;
	width: 40px;
	height: 40px;
	border-radius: 2px;
	background-color: #fff;
}

.google-icon {
	position: absolute;
	margin-top: 11px;
	margin-left: -8px;
	width: 18px;
	height: 18px;
}

.btn-text {
	float: right;
	color: #fff;
	font-size: 14px;
	letter-spacing: 0.2px;
	font-family: 'Roboto';
	background-color: #4285f4;
	width: 80%;
	height: 100%;
	text-align: center;
}

.btn-text:hover {
  filter: none;
}

/* Make clicks pass-through */
#nprogress {
	pointer-events: none;
}

#nprogress .bar {
	background: #29d;

	position: fixed;
	z-index: 1031;
	top: 0;
	left: 0;

	width: 100%;
	height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
	display: block;
	position: absolute;
	right: 0px;
	width: 100px;
	height: 100%;
	box-shadow: 0 0 10px #29d, 0 0 5px #29d;
	opacity: 1;
	transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
	display: block;
	position: fixed;
	z-index: 1031;
	top: 15px;
	right: 15px;
}

#nprogress .spinner-icon {
	width: 18px;
	height: 18px;
	box-sizing: border-box;

	border: solid 2px transparent;
	border-top-color: #29d;
	border-left-color: #29d;
	border-radius: 50%;
	animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
	overflow: hidden;
	position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
	position: absolute;
}
@keyframes nprogress-spinner {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

